import React, { component } from "react";

export default class Gallery extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      extend: false,
      index: 0
    };
  }
  handleExtend = index => {
    this.setState({ extend: true, index });
  };
  handleCollapse = () => {
    this.setState({ extend: false, index: 0 });
  };
  handleNextImage = () => {
    this.setState(state => ({ index: this.state.index + 1 }));
  };
  handleLastImage = () => {
    this.setState(state => ({ index: this.state.index - 1 }));
  };
  componentDidUpdate = () => {};
  render() {
    return (
      <div>
        {this.state.extend ? (
          <div className="slideshow-container">
            <button
              onClick={this.state.index > 0 ? this.handleLastImage : null}
            >
              Last
            </button>
            <div className="img-container">
              <img
                className="large"
                src={"/static/gallery/" + this.props.images[this.state.index]}
              />
              <div id="collapse-button-container">
                <button onClick={this.handleCollapse}>X</button>
              </div>
            </div>
            <button
              onClick={
                this.state.index < this.props.images.length - 1
                  ? this.handleNextImage
                  : null
              }
            >
              Next
            </button>
          </div>
        ) : (
          <div className="container">
            <h1>Gallery</h1>
            <p>This is a gallery showcasing some of my photography!</p>
            <div className="masonry">
              {this.props.images.map((image, index) => (
                <img
                  onClick={() => this.handleExtend(index)}
                  key={index}
                  className="item"
                  src={"/static/gallery/" + image}
                />
              ))}
            </div>
          </div>
        )}

        <style jsx>{`
          .container {
            display: flex;
            flex-direction: column;
            align-items: center;
            padding: 0 5% 0 5%;
          }
          #collapse-button-container {
            position: absolute;
            top: 5%;
            width: 100%;
            display: flex;
            justify-content: center;
          }
          .img-container {
            position: relative;
            background: black;
            height: 90vh;
            display: flex;
            justify-content: center;
            align-items: center;
            text-align: center;
          }
          .slideshow-container {
            display: grid;
            grid-template-columns: 1fr 20fr 1fr;
            width: 100%;
            height: 90vh;
          }
          button {
            background: black;
            border: none;
            color: white;
          }
          button:hover {
            opacity: 0.65;
          }
          .masonry {
            column-count: 4;
            column-gap: 0.5em;
          }
          .item {
            display: inline-block;
            background-color: #eee;
            margin: 0 0 0.5em;
            width: 100%;
          }
          .large {
            max-height: 100%;
            max-width: 100%;
          }
        `}</style>
      </div>
    );
  }
}
